body {
  background-color: #c5b38c;
  width: 100%;
}

.container {
  margin-inline: auto;
  width: min(90%, 90.5rem);
  font-size: large;
}

.sub-container {
  display: flex;
  /* flex-direction: row; */
}

.item {
  flex-grow: 1;
  flex-basis: 0;
}

.big-title {
  font-size: 50px;
  text-align: center;
  font-weight: bolder;
  margin-top: 7rem;
  margin-bottom: 5rem;
}

.italic {
  font-style: oblique;
}

.indent {
  text-indent: 2rem;
  margin-left: 4rem;
}

.margin-left-two-rem {
  margin-left: 2rem;
}

.bold {
  font-weight: bold;
}

a {
  text-decoration: none;
  position: relative;
  color: #405f72;
  font-weight: 500;
}
a::before {
  content: '';
  position: absolute;
  bottom: -2px;
  right: 0;
  width: 0;
  height: 1.5px;
  background-color: #0e5acb;
  transition: width 0.5s cubic-bezier(0.25, 1, 0.5, 1);
}
a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
  background-color: #1976ff;
}
a:active {
  text-decoration: none;
  color: #0a46a5;
}

.left-space {
  margin-left: 20px;
}

.center {
  text-align: center;
  margin-left: 5%;
}

.box {
  background-color: #7c897e;
}

h1 {
  max-width: fit-content;
  padding: 10px;
}

h2 {
  font-weight: 500;
}

.underline {
  background-color: #9fa096;
  border-bottom: 0.1px solid #9fa096;
  border-top: 1px solid #9fa096;
}

.chart-title {
  margin-left: 40%;
  font-weight: 600;
}

.hide {
  display: none;
}

.hover-display:hover + .hide {
  display: block;
  color: #463b57;
  overflow: visible;
}

.hover-display {
  color: #404672;
}

.red {
  color: #c70039;
}
